.team-header{
    padding: 2rem;
}

.team-header > div{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.team-header > div > img{
    width: 4rem;
    max-height: 4rem;
}

table{
    margin: 1rem;
}

table > thead > tr > th {
    font-weight: 700;
    padding: 0.5rem;
}

table > tbody > tr > td {
    text-align: center;
    padding: 0.5rem;
}

tr:nth-child(even) {
    background-color: rgba(0, 0, 0 , 0.25);
}

button {
    background-color: var(--primary-color);
    padding: 1rem;
    border-radius: 0.5rem;
    font-size: larger;
    font-weight: 700;
}